  justify-content: flex-start;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  transition: 0.2s;
  will-change: transform; }

.feedback-slider__list-item {
  flex-shrink: 0;
  margin-right: 30px; }
  .feedback-slider__list-item--white .feedback {
    background-color: #ffffff;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1); }
    @media (max-width: 1210px) {
      .feedback-slider__list-item--white .feedback {
        box-shadow: 0 10px 10px rgba(0, 0, 0, 0.1); } }
    @media (max-width: 420px) {
      .feedback-slider__list-item--white .feedback {
        background-color: #f5f5f5;
        box-shadow: none; } }

.feedback-slider__button {
  position: absolute;
  z-index: 5;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  margin: 0;
  padding: 0;
  background-color: #ffffff;
  border: 1px solid #eeeeee;
  border-radius: 50%;
  cursor: pointer;
  transition: 0.3s; }
  .feedback-slider__button--preview {
    left: -60px; }
    @media (max-width: 1400px) {
      .feedback-slider__button--preview {
        left: 10px; } }
    @media (max-width: 1210px) {
      .feedback-slider__button--preview {
        left: -35px; } }
    @media (max-width: 500px) {
      .feedback-slider__button--preview {
        left: 10px; } }
  .feedback-slider__button--forward {
    right: -60px; }
    @media (max-width: 1400px) {
      .feedback-slider__button--forward {
        right: 10px; } }
    @media (max-width: 1210px) {
      .feedback-slider__button--forward {
        right: -35px; } }
    @media (max-width: 500px) {
      .feedback-slider__button--forward {
        right: 10px; } }
  .feedback-slider__button:focus, .feedback-slider__button:hover {
    outline: none;
    background-image: linear-gradient(to right, #fd712c 0%, #f21780 100%);
    border: none;
    box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.1); }
    .feedback-slider__button:focus:focus .feedback-slider__button-icon > path,
    .feedback-slider__button:focus:hover .feedback-slider__button-icon > path, .feedback-slider__button:hover:focus .feedback-slider__button-icon > path,
    .feedback-slider__button:hover:hover .feedback-slider__button-icon > path {
      fill: #ffffff; }

.feedback-slider__button-icon {
  width: 15px;
  height: 7px; }

.feedback {
  display: grid;
  grid-template-columns: 80px 1fr;
  grid-template-rows: min-content 80px;
  grid-template-areas: "quote quote" "picture author";
  gap: 22px 20px;
  width: 360px;
  margin: 0;
  padding: 36px 40px;
  background-color: #f7fafc; }
  @media (max-width: 420px) {
    .feedback {
      grid-template-columns: 1fr;
      grid-template-rows: repeat(3, min-content);
      grid-template-areas: "quote" "picture" "author";
      width: 280px; } }

.feedback__quote {
  grid-area: quote;
  margin: 0;
  font-weight: 300; }

.feedback__picture {
  grid-area: picture;
  display: block;
  width: 80px;
  height: 80px; }
  @media (max-width: 420px) {
    .feedback__picture {
      justify-self: center; } }

.feedback__img {
  display: block;
  border-radius: 50%; }

.feedback__author {
  grid-area: author;
  align-self: center;
  font-style: normal; }
  @media (max-width: 420px) {
    .feedback__author {
      justify-self: center; } }

.feedback__name {
  color: #222222;
  font-family: Dosis;
  font-size: 18px;
  font-weight: 400; }

.feedback__company {
  font-weight: 300; }

.download-section {
  padding-top: 116px;
  padding-bottom: 89px;
  background: linear-gradient(to right, #fd712c 0%, #f21780 100%); }
  @media (max-width: 768px) {
    .download-section {
      padding-top: 50px;
      padding-bottom: 20px; } }

.download-section__title {
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 88px;
  color: #ffffff;
  text-align: center; }

.download-section__list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 0;
  margin-right: -30px;
  padding: 0;
  list-style: none; }

.download-section__item {
  margin-right: 30px;
  margin-bottom: 30px; }

.download {
  display: grid;
  grid-template-columns: min-content 1fr;
  grid-template-rows: min-content min-content;
  grid-template-areas: "icon title" "icon description";
  gap: 11px 18px;
  width: 360px;
  background-color: #ffffff;
  border-radius: 3px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.07);
  transition: 0.3s; }
  @media (max-width: 420px) {
    .download {
      width: 300px; } }
  .download:focus {
    outline: none; }
  .download:focus, .download:hover {
    transform: scale(1.05);
    box-shadow: 0 0 0 2px #f21780, 0 0 0 5px #ffffff; }
  .download:focus g,
  .download:hover g {
    fill: url(#gradient); }

.download__icon {
  grid-area: icon;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 115px;
  height: 115px;
  background-color: #f7fafc;
  border-radius: 3px; }
  @media (max-width: 420px) {
    .download__icon {
      width: 95px;
      height: 95px; } }

.download__title {
  grid-area: title;
  align-self: end;
  margin: 0;
  color: #222222;
  font-family: Dosis;
  font-size: 30px;
  font-weight: 600; }

.download__description {
  grid-area: description;
  align-self: start;
  margin: 0;
  font-weight: 300; }

.footer {
  padding-top: 58px;
  padding-bottom: 53px;
  background-color: #222222; }

.footer__socials {
  margin: 0;
  margin-bottom: 28px; }

.footer__copyright {
  margin: 0;
  padding: 0 15px;
  color: #ffffff;
  font-weight: 300;
  text-align: center; }

.socials {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  padding: 0;
  list-style: none; }

.socials__item:not(:last-of-type) {
  margin-right: 30px; }

.socials__link {
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.3s; }
  .socials__link:focus {
    outline: none; }
  .socials__link:focus, .socials__link:hover {
    transform: scale(1.2); }
  .socials__link:focus path,
  .socials__link:hover path {
    fill: url(#gradient-link); }
